import request from '../request'


// 获取列表
export function listProblem(data) {
    return request({
        url: '/api/problem/listProblem',
        method: 'post',
        data
    })
}

// 新增
export function saveProblem(data) {
    return request({
        url: '/api/problem/saveProblem',
        method: 'post',
        data
    })
}

// 修改
export function updateProblem(data) {
    return request({
        url: '/api/problem/updateProblem',
        method: 'post',
        data
    })
}

// 删除
export function deleteProblemById(params) {
    return request({
        url: '/api/problem/deleteProblemById',
        method: 'get',
        params: params
    });
}
