<template>
  <div id="style-operate">
    <!-- Form -->
    <el-dialog v-model="dialogFormVisible" :before-close="beforeClose" :title="titleName" width="700" draggable>
      <!-- Form 自定义表单 -->
      <el-form :model="form" :rules="rules" ref="ruleForm">
        <el-form-item :label="$t('problemView.icon')" prop="icon" :label-width="formLabelWidth">
          <el-input v-model="form.icon" :placeholder="$t('problemView.iconPlaceholder')" autocomplete="off" color="#626aef" >
            <template #append >
              <el-button icon="Search" @click="handleIconClick" class="custom-button"></el-button>
            </template>
          </el-input>
        </el-form-item>

        <el-form-item :label="$t('problemView.title')" prop="title" :label-width="formLabelWidth">
          <el-input :readonly="isReadonly" v-model="form.title" :placeholder="$t('problemView.titlePlaceholder')" autocomplete="off"/>
        </el-form-item>
        <el-form-item :label="$t('problemView.titleEn')" prop="titleEn" :label-width="formLabelWidth">
          <el-input :readonly="isReadonly" v-model="form.titleEn" :placeholder="$t('problemView.titleEnPlaceholder')" autocomplete="off"/>
        </el-form-item>

        <el-form-item :label="$t('problemView.remarks')" prop="remarks" :label-width="formLabelWidth">
          <el-input type="textarea" :rows="2" :readonly="isReadonly" v-model="form.remarks" :placeholder="$t('problemView.remarksPlaceholder')" style="width: 65%" autocomplete="off"/>
        </el-form-item>
        <el-form-item :label="$t('problemView.remarksEn')" prop="remarksEn" :label-width="formLabelWidth">
          <el-input type="textarea" :rows="2" :readonly="isReadonly" v-model="form.remarksEn" :placeholder="$t('problemView.remarksEnPlaceholder')" style="width: 65%" autocomplete="off"/>
        </el-form-item>
      </el-form>

      <template #footer>
      <span class="dialog-footer">
        <!-- Form 按钮 -->
        <el-button @click="closeButton()">
          {{ $t('common.button.cancel') }}
        </el-button>
        <el-button type="primary" v-show="type != 'detail'" @click="submitButton()">
          {{ $t('common.button.confirm') }}
        </el-button>
      </span>
      </template>
    </el-dialog>
    <!-- 图标选择组件 -->
    <iconView ref="icon" @childClick="setIcon"/>
  </div>
</template>

<script>
import {ElNotification} from "element-plus";
import {saveProblem, updateProblem} from "@/http/clientPage/problemApi";
import iconView from "@/views/common/iconView";

export default {
  name: "style-operate",
  components: {iconView},
  // data: 对象就是要渲染到页面上的数据
  data: function () {
    return {
      // 控制对话框大开闭
      dialogFormVisible: false,
      // 联动列表数据 - 行数据副本
      rowData: {},
      form: {},
      rules: {
        icon: [
          {required: true, message: this.$t('problemView.iconPlaceholder'), trigger: 'change'},
        ],
        title: [
          {required: true, message: this.$t('problemView.titlePlaceholder'), trigger: 'blur'},
        ],
        titleEn: [
          {required: true, message: this.$t('problemView.titleEnPlaceholder'), trigger: 'blur'},
        ],
        remarks: [
          {required: true, message: this.$t('problemView.remarksPlaceholder'), trigger: 'blur'},
        ],
        remarksEn: [
          {required: true, message: this.$t('problemView.remarksEnPlaceholder'), trigger: 'blur'},
        ],
      },
      // 进入类型: 新增 详情 编辑
      type: '',
      titleName: this.$t('common.dialog.defaultTitle'),
      // 表单的只读
      isReadonly: false,
      formLabelWidth: '200px'
    }
  },

  // watch 侦听器 能够侦听到data数据的变化 且获取对应的 新值 和 旧值
  watch: {
    type(newVal, oldVal) {
      if (newVal == '') return
      if (newVal == 'add') this.titleName = this.$t('common.dialog.addTitle')
      if (newVal == 'edit') this.titleName = this.$t('common.dialog.editTitle')
      if (newVal == 'detail') this.titleName = this.$t('common.dialog.detailTitle')
    }
  },

  // moubted: 页面加载完毕后执行
  mounted() {

  },

  // methods: 对象中的方法，可以在页面中调用
  methods: {
    // 这里是图标点击事件的处理逻辑
    handleIconClick() {
      if (this.isReadonly) return
      this.$refs.icon.drawer = true;
      if (this.form.icon != null) {
        this.$refs.icon.iconStr = this.form.icon;
      }
    },
    // 图标选择
    setIcon(icon) {
      this.form.icon = icon;
    },
    // 点击遮罩和x时执行
    beforeClose(done) {
      this.closeButton();
      done(); // 必须调用 done，以继续关闭对话框
    },
    // 取消按钮
    closeButton() {
      this.dialogFormVisible= false;
      this.isReadonly= false;
      this.form= {};
      this.rowData= {};
      this.$refs.ruleForm.resetFields(); // 重置表单验证
    },
    // 确认按钮
    submitButton() {
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          if (this.type == "add") {
            saveProblem(this.form).then(res => {
              if (res.data.status == 200) {
                this.dialogFormVisible= false;
                ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
                // 刷新列表
                this.$emit('operateClick');
              }
            }).catch((erro) => { });
          } else if (this.type == "edit") {
            updateProblem(this.form).then(res => {
              if (res.data.status == 200) {
                this.dialogFormVisible= false;
                ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
                // 刷新列表
                this.$emit('operateClick');
              }
            }).catch((erro) => { });
          }
          this.closeButton();
        } else {
          return false;
        }
      });
    }
  },
}
</script>

<!--组件css样式 scoped:表示该样式只在此组件生效-->
<style scoped>
.el-button--text {
  margin-right: 15px;
}
.el-select {
  width: 300px;
}
.el-input {
  width: 300px;
}
.dialog-footer button:first-child {
  margin-right: 10px;
}
</style>
